* {
  color: inherit;
  font-size: inherit;
  scroll-behavior: smooth;
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
.App {
  position: fixed;
  text-align: center;
  background-color: #f7f8fc;
  /* margin: 0; */
  height: 100%;
  width: 100%;
}

.border-div {
  position: relative;
  left: 7%;
  width: 81%;
  padding-top: 1px;
  margin-bottom: 10px;
  border-bottom: 1.5px solid #f1f1f1;
}

.container-div {
  position: relative;
  background-color: white;
  border-radius: 5px;
  margin-top: 15px;
  margin-bottom: 20px;
  width: 95%;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 25px;
  padding-top: 25px;
  box-shadow: 1px 1px 7px rgba(228, 228, 228, 0.734);
}

.title-div {
  position: relative;
  width: 84%;
  left: -2%;
  display: flex;
  justify-content: space-between;
  margin-left: auto;
  margin-right: auto;
}

.alumno-row {
  width: 87%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 5px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

input {
  border-color: #e9e9e9;
  border-style: solid;
  border-radius: 5px;
  border-width: 1px;
  padding-left: 4px;
  padding-right: 4px;
}

input:focus {
  outline-color: #f1f1f1;
  border-width: 1px;
}

.long-input {
  width: 26%;
}

.col-div {
  width: 87%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 17px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.col-div span {
  font-weight: 400;
  margin: 5px;
  position: inherit;
}

.delete-button {
  visibility: hidden;
  position: relative;
  text-align: center;
  left: -20px;
  top: 1px;
  width: 15px;
  height: 15px;
  border-radius: 100%;
  background-color: rgba(220, 220, 220, 0);
  border-style: hidden;
  color: #333;
  font-size: 15px;
}

.delete-button img {
  filter: invert(40%);
  width: 13px;
}

.alumno-row:hover .delete-button {
  visibility: visible;
}

.delete-button:hover {
  cursor: pointer;
}

.add-button {
  position: relative;
  left: 0%;
  top: 1px;
  width: 23px;
  height: 23px;
  border-radius: 100%;
  background-color: rgba(240, 240, 240, 0.193);
  color: #232323;
  font-size: 15px;
  border-color: #e9e9e9;
  border-style: solid;
  border-width: 1px;
}

.add-button:hover {
  cursor: pointer;
}

.print-button {
  position: relative;
  left: 39.1%;
  border-radius: 100%;
  background-color: white;
  color: #333;
  width: 40px;
  height: 40px;
  border-color: #e9e9e9;
  border-style: none;
  border-width: 1px;
  box-shadow: 1px 1px 7px rgba(228, 228, 228, 0.734);
}

.print-button:hover {
  cursor: pointer;
}

.print-button img {
  filter: invert(15%);
}

@page {
  size: 13in 18.33in;
  margin-left: auto;
  margin-right: auto;
  margin-top: 5%;
}

@media only screen and (max-width: 700px) {
  * {
    font-size: 5px;
  }
  .col-div {
    width: 92%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 17px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
  }

  .alumno-row {
    width: 92%;
    margin-left: auto;
    margin-right: auto;
    padding-top: 5px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }
}
